import React, { useState } from 'react'
import AdminServices from 'services/api/AdminServices'
import Tabs from 'views/mobileView/components/atoms/Tabs';
import MainHeader from "views/mobileView/components/molecules/Headers/MainHeader";
import cssVar from 'views/mobileView/styles';
import ReadMoreText from 'views/mobileView/components/atoms/ReadMoreText'
import { AiOutlineDown, AiOutlineExclamationCircle, AiOutlineFileSearch, AiOutlineQuestionCircle, AiOutlineRight, AiOutlineUp } from 'react-icons/ai';
import { toast } from 'react-toastify';
import db from 'libs/db';
import FundConstituents from 'views/mobileView/components/molecules/Cards/FundConstituents'
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import { changeObjKey } from 'views/mobileView/components/atoms/DataCustomizer';
import addToRecentlyViewed from 'views/mobileView/components/atoms/addToRecentlyViewed';
import { RiskFormat } from 'views/mobileView/components/atoms/RiskFunctions'
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { minInvestment } from 'views/mobileView/components/atoms/MinInvestment';
import { FaBalanceScaleRight } from 'react-icons/fa';
import { FiBriefcase } from 'react-icons/fi';
import RedGreenText from 'views/mobileView/components/atoms/RedGreenText';
import { BiRefresh } from 'react-icons/bi';
import SliderComponent2 from 'views/mobileView/components/atoms/SliderComponent2';
import PerformanceChart from 'views/mobileView/components/molecules/Cards/PerformanceChart';
// import YouTube from 'react-youtube';
import Modal from 'react-bootstrap/Modal';
import { baseImgURL } from 'assets/Constant';
function MintboxDetail(props) {
  const MINTBOXID = Number(props.match.params.MintboxId);

  const [mintbox, setMintbox] = useState({});
  const [portfolio, setPortfolio] = React.useState({});
  const [portfolioXIRR, setPortfolioXIRR] = React.useState({});

  const [otherRiskMintboxes, setOtherRiskMintboxes] = React.useState([]);
  const [otherDurationMintboxes, setOtherDurationMintboxes] = React.useState([]);
  const [mintboxConstituents, setMintboxConstituents] = useState([]);
  const [isWatchlisted, setIsWatchlisted] = useState(false);
  const [user, setUser] = useState({})
  const [userRisk, setUserRisk] = useState(0)

  const [loading, setLoading] = React.useState(true);
  const [showVideoModal, setShowVideoModal] = React.useState(false);
  React.useEffect(async () => {
    await loadData();
  }, [MINTBOXID])

  const loadData = async () => {
    setLoading(true)
    try {
      db.get(db.model.user).then(async (p) => {
        setUser(p.userData);
        await getWatchlist(p.userData.id)
        await getMintboxById().then((mintbox) => {
          getSuggestedMintboxes(mintbox, p.userData.id)
          addToRecentlyViewed({
            type: 3,
            ID: MINTBOXID,
            Name: mintbox.MintboxName,
            XIRR: mintbox.XIRR,
            Duration: mintbox.Duration,
            RiskLevel: mintbox.RiskLevel,
            About: mintbox.About,
            MinInv: mintbox.MinSIPAmount
          })
        });
        await getClientData(p.userData.id)
        await getPortfolio(p.userData.id)
        await getPortfolioXIRR(p.userData.id)
        await getMintboxConstituentsById();
        setLoading(false)
      })
    } catch (error) {
      console.log(error)
      setLoading(false)
    }
  }

  const getClientData = async (id) => {
    await db.get(db.model.clientData).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getClient(id).then(res => res);
        if (res.type === true) {
          const a = JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get Client API :", aa)
          if (aa.RiskInfo.length) {
            setUserRisk(aa.RiskInfo[0].RiskScore)
          }
          await db.set(db.model.clientData, JSON.stringify(aa))
        }
      } else {
        if (p.RiskInfo.length) {
          setUserRisk(p.RiskInfo[0].RiskScore)
        }
      }
    })
  };

  const getPortfolio = async (id) => {
    await db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          setPortfolio(aa)
          await db.set(db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        setPortfolio(p)
      }
    })
  }

  const getPortfolioXIRR = async (id) => {
    await db.get(db.model.portfolioXirr).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioXIRR(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          const aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio XIRR API :", aa)
          setPortfolioXIRR(aa)
          await db.set(db.model.portfolioXirr, JSON.stringify(aa))
        }
      } else {
        setPortfolioXIRR(p)
      }
    })
  }

  const logNewMinimumInv = async (selectedETFs) => {
    console.log("1")
    var etfsData = await AdminServices.getETFsById(selectedETFs.map(etf => etf.Symbol).toString()).then(res => res)
    let currentNav = {};
    if (etfsData.type) {
      etfsData.data.map(item => {
        currentNav[item.Symbol] = item.NAV
      })
    }
    selectedETFs = selectedETFs.map(f => {
      return {
        ...f,
        allocation: f.CurrentWeightage,
        NAV: currentNav[f.Symbol]
      }
    })
    console.log("new min inv is", MINTBOXID, minInvestment(selectedETFs))
  }

  const getMintboxById = async () => {
    let res = await AdminServices.getMintboxById(MINTBOXID).then(res => res)
    if (res.type) {
      console.log("Get Mintbox by id API", JSON.parse(res.data))
      const a = await JSON.parse(res.data);
      let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
      console.log(aa.MintboxInfo[0])
      setMintbox(aa.MintboxInfo[0])
      return aa.MintboxInfo[0]
    } else {
      toast.error("Error")
    }
  }

  const getMintboxConstituentsById = async () => {
    let res = await AdminServices.getMintboxConstituentsById(MINTBOXID).then(res => res)
    if (res.type) {
      console.log("Get mintbox constituents API", res.data)
      setMintboxConstituents(res.data)
      // logNewMinimumInv(res.data)
    } else {
      toast.error("Error")
    }
  }
  const getSuggestedMintboxes = async (mintbox, id) => {
    await db.get(db.model.allMintboxes).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getAllMintboxes(id).then(res => res);
        if (res.type === true) {
          console.log("Get All Mintboxes API: ", res.data)
          await db.set(db.model.allMintboxes, JSON.stringify(res.data))
          setOtherRiskMintboxes(res.data.filter(m => m.RiskLevel === mintbox.RiskLevel && m.MintboxId !== MINTBOXID))
          setOtherDurationMintboxes(res.data.filter(m => m.DurationId === mintbox.IdealDuration && m.MintboxId !== MINTBOXID))
        }
      } else {
        console.log(mintbox)
        setOtherRiskMintboxes(p.filter(m => m.RiskLevel === mintbox.RiskLevel && m.MintboxId !== MINTBOXID))
        setOtherDurationMintboxes(p.filter(m => m.DurationId === mintbox.IdealDuration && m.MintboxId !== MINTBOXID))
      }
    })
  }

  const getWatchlist = async (id) => {
    try {
      await db.get(db.model.watchlist).then(async (p) => {
        console.log(p)
        if (p === null) {
          let res = await AdminServices.getWatchlist(id).then(res => res);
          if (res.type === true) {
            await db.set(db.model.watchlist, JSON.stringify(res.data))
            setIsWatchlisted(res.data.some(f => f.FundType === 3 && Number(f.FundCode) === MINTBOXID))
          }
        } else {
          setIsWatchlisted(p.some(f => f.FundType === 3 && Number(f.FundCode) === MINTBOXID))
        }
      });
    } catch (error) {
      toast.error(error);
    }
  }

  const updateWatchlist = async () => {
    const data = {
      "clientId": user.id,
      "fundType": 3,
      "fundCode": MINTBOXID + "",
      "isActive": isWatchlisted ? 0 : 1
    }
    console.log("updateWatchlist", data)
    try {
      let res = await AdminServices.updateWatchlist(data).then(res => res)
      if (res.type) {
        setIsWatchlisted(!isWatchlisted)
      }
    } catch (error) {
      console.log(error)
    }

  }

  const onRefresh = () => {
    db.remove(db.model.portfolio);
    db.remove(db.model.portfolioXirr);
    getPortfolio(user.id)
    getPortfolioXIRR(user.id)
  }

  const VideoModal = () => {
    return (
      <Modal
        className="bottom-drawer"
        show={showVideoModal}
        onHide={() => setShowVideoModal(false)}
        keyboard={false}
      >
        <Modal.Body>
          <iframe width="100%" height="300" src={`https://www.youtube.com/embed/${mintbox?.AboutVideoLink}`} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen style={{ borderRadius: "8px", marginTop: "12px" }}></iframe>
        </Modal.Body>
        <Modal.Footer>
          <button className="solidButton" onClick={() => setShowVideoModal(false)}>
            Close
          </button>
        </Modal.Footer>
      </Modal>
    )
  }

  const Summary = () => {
    return (
      <>
        <div style={{ background: cssVar.getPropertyValue('--darkGreen'), padding: "4%", display: "flex", gap: "4%" }}>
          <MintboxIcon risk={mintbox.RiskLevel} height="42px" width="42px" />
          <div style={{ flex: 1 }}>
            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white'), minHeight: "44px" }}>{mintbox.MintboxName}</div>
            {
              mintbox.IsCustom === 1 ?
                <></>
                :
                <div style={{ display: "flex", marginTop: "4%" }}>
                  <div style={{ flex: 1 }}>
                    <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>5Y CAGR</div>
                    <div className="header1dark" style={{ color: cssVar.getPropertyValue("--primaryGreen") }}>{parseFloat(mintbox.XIRR || 0).toFixed(2)} %</div>
                  </div>
                  <div style={{ flex: 1 }}>
                    <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>Risk level</div>
                    <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{RiskFormat(mintbox.RiskLevel)}</div>
                  </div>
                </div>
            }
            <div style={{ display: "flex", marginTop: "4%" }}>
              {
                mintbox.IsCustom === 1 ?
                  <></>
                  :
                  <div style={{ flex: 1 }}>
                    <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>Ideal for</div>
                    <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{mintbox.Duration}</div>
                  </div>
              }
              <div style={{ flex: 1 }}>
                <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>Min investment</div>
                <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{CurrencyFormat(mintbox.MinSIPAmount, 'long', 2)}</div>
              </div>
            </div>
          </div>
        </div>
        <IsSuitableMintbox />
      </>
    )
  }

  const IsSuitableMintbox = () => {
    const isSuitable = mintbox.RiskLevel === userRisk;
    const isInvested = portfolio.MintboxConsolidated?.find(m => m.MintboxId === MINTBOXID && m.GoalId === 0 && m.MintboxBalance !== 0)
    const [show, setShow] = React.useState(false);

    const balance = portfolio.MintboxConsolidated?.find(f => f.MintboxId === MINTBOXID)?.MintboxBalance
    const previousBalance = portfolio.PrevMintboxConsolidated?.find(f => f.MintboxId === MINTBOXID)?.PrevMintboxBalance
    const xirr = Object.values(portfolioXIRR?.MintboxXIRR || {}).find(m => m.MintboxId === MINTBOXID)?.XIRR
    let change = 0;
    let changePercentage = 0;
    if (previousBalance !== 0 && previousBalance !== '') {
      change = balance - previousBalance;
      changePercentage = change * 100 / previousBalance;
    }

    if (!isInvested && mintbox.IsCustom !== 0) {
      return <></>
    }
    return <div style={{ padding: '4%', margin: '4%', background: cssVar.getPropertyValue('--lightest'), borderRadius: '8px' }}>
      <div style={{ display: 'flex', gap: '2%', alignItems: 'center' }} onClick={() => setShow(!show)}>
        {
          isInvested ?
            <FiBriefcase size={20} style={{ color: cssVar.getPropertyValue('--black') }} />
            :
            isSuitable ?
              <img src={baseImgURL+"like-black.webp"} width='20px' height='20px' />
              :
              userRisk === 0 ?
                <AiOutlineQuestionCircle size={20} />
                :
                <AiOutlineExclamationCircle size={20} />
        }
        <div className='header2dark' style={{ flex: 1 }}>
          {
            isInvested ? 'This mintbox is in your portfolio' :
              isSuitable ? 'This mintbox is suitable for you' :
                userRisk === 0 ? 'Is this mintbox for you?'
                  : 'This mintbox is not suitable for you'}
        </div>
        {
          show ?
            <AiOutlineUp size={14} className='secondaryGreen' />
            :
            <AiOutlineDown size={14} className='secondaryGreen' />
        }
      </div>
      {
        show ?
          isInvested ?
            <div style={{ display: "flex", paddingTop: "4%" }}>
              <div style={{ flex: 1 }}>
                <div className='bigHeader' style={{ display: 'flex', alignItems: 'center', gap: "10px" }}>
                  {CurrencyFormat(balance)} <BiRefresh size={24} className='secondaryGreen' onClick={() => onRefresh()} />
                </div>
                <div>
                  <span className='smallTextLight'>1 Day </span>
                  <span className='smallTextDark'>
                    <RedGreenText text={CurrencyFormat(change, 'long', 2)} value={change} />
                    <RedGreenText text={"(" + changePercentage.toFixed(2) + "%)"} value={changePercentage} />
                  </span>
                </div>
              </div>
              <div style={{ textAlign: 'right' }}>
                <div className='bodyTextLight'>
                  Annualized return
                </div>
                <div className='header1dark'>
                  <RedGreenText text={xirr.toFixed(2) + '%'} value={xirr} />
                </div>
              </div>
            </div>
            :
            isSuitable ?
              <div className='smallTextLight' style={{ marginTop: '2%' }}>
                {`Because your risk profile is '${RiskFormat(userRisk)}'`}
              </div>
              :
              userRisk === 0 ?
                <>
                  <div className='smallTextLight' style={{ marginTop: '2%' }}>
                    Each mintbox is designed for investors of a specific risk level with a specific investment period. Answer a few questions and we will tell you if this mintbox is for you!
                  </div>
                  <div className='header2dark secondaryGreen' style={{ marginTop: '2%' }} onClick={() => props.history.push('/risk-profile')}>
                    Find out if this mintbox is for you <AiOutlineRight size={10} />
                  </div>
                </>
                :
                <>
                  <div className='smallTextLight' style={{ marginTop: '2%' }}>
                    {`Because your risk profile is '${RiskFormat(userRisk)}' and this mintbox is made for '${RiskFormat(mintbox.RiskLevel)}' investors`}
                  </div>
                  <div className='header2dark secondaryGreen' style={{ marginTop: '2%' }} onClick={() => props.history.push({
                    pathname: '/list',
                    state: { type: 3, query: 'RiskLevel', RISKLEVEL: userRisk }
                  })}>
                    Show mintboxes suitable for me <AiOutlineRight size={10} />
                  </div>
                </>
          :
          <></>
      }
    </div>
  }
  const Overview = () => {
    return (
      <div style={{ padding: "6% 4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
        <div className='header2dark' style={{ marginBottom: "2%" }}>
          About this mintbox
        </div>
        <div className='bodyTextLight' style={{ whiteSpace: 'pre-line' }}>
          <ReadMoreText text={mintbox.About?.replaceAll(`\{\"\\n\"\}`, '\n\n')} />
        </div>
      </div>
    )
  }

  const Constituents = () => {
    return <>
      {
        mintbox.IsCustom !== 1 && mintboxConstituents.some(c => c.AssetClassId === 3) ?
          <div style={{ margin: '4%', padding: '4%', backgroundColor: cssVar.getPropertyValue('--lightest'), borderRadius: '8px' }}>
            <div style={{ display: 'flex', gap: '3%', alignItems: 'center', marginBottom: '2%' }}>
              <AiOutlineExclamationCircle size={16} style={{ color: cssVar.getPropertyValue('--internationalEquity') }} />
              <div className='header2dark'>Important note</div>
            </div>
            <div className='smallTextLight'>
              This mintboxs constituents include US ETFs that are trading at premium due to SEBIs restrictions. Once the restrictions are lifted, we will add US ETFs through rebalancing.
            </div>
          </div>
          :
          <></>
      }
      <FundConstituents fundConstituents={mintboxConstituents} />
      <div style={{ margin: '4%', padding: '4%', backgroundColor: cssVar.getPropertyValue('--lightest'), borderRadius: '8px', display: 'flex', gap: '8px', flexDirection: 'column' }}>
        <div className='header2dark'>
          <FaBalanceScaleRight size={20} style={{ marginRight: '3%' }} />Rebalancing
        </div>
        <div className='bodyTextLight'>
          The constituents of this mintbox will be <b>rebalanced {mintbox.RebalanceFrequency === 1 ? "every quater" : mintbox.RebalanceFrequency === 2 ? "half-yearly" : mintbox.RebalanceFrequency === 3 ? "yearly" : "manually"}</b> to reflect the underlying investment methodology
        </div>
        {/* <div className='header2dark secondaryGreen'>
          How rebalancing works <AiOutlineRight size={12} />
        </div> */}
      </div>
      {
        mintbox.ReviewFrequency === 0 ?
          <></>
          :
          <div style={{ margin: '4%', padding: '4%', backgroundColor: cssVar.getPropertyValue('--lightest'), borderRadius: '8px', display: 'flex', gap: '8px', flexDirection: 'column' }}>
            <div className='header2dark'>
              <AiOutlineFileSearch size={20} style={{ marginRight: '3%' }} />Reviewing
            </div>
            <div className='bodyTextLight'>
              This mintbox will be <b>reviewed {mintbox.ReviewFrequency === 1 ? "every quater" : mintbox.ReviewFrequency === 2 ? "half-yearly" : mintbox.ReviewFrequency === 3 ? "yearly" : "manually"}</b> to update the underlying methodology and replace constitutents if required
            </div>
            {/* <div className='header2dark secondaryGreen'>
              How rebalancing works <AiOutlineRight size={12} />
            </div> */}
          </div>
      }
    </>
  }

  const Fee = () => {
    return (
      <div style={{ padding: "6% 4% 0 4%" }}>
        <div style={{ padding: '2% 4%', border: '1px solid ' + cssVar.getPropertyValue('--light'), borderRadius: '8px' }}>
          <div className='bodyTextDark' style={{ display: 'flex', padding: '3% 0', borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
            <div style={{ flex: 1 }}>Avg expense ratio of underlying ETFs</div>
            <div style={{ paddingRight: '4%' }}>{Number(mintbox?.MintboxCost)?.toFixed(2) || 0.00}%</div>
          </div>
          <div style={{ display: 'flex', padding: '3% 0', borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
            <div className='bodyTextDark' style={{ flex: 1 }}>mintbox management fee</div>
            <div className='header2dark secondaryGreen' style={{ paddingRight: '4%' }}>0.00%</div>
          </div>
          <div className='header2dark' style={{ display: 'flex', padding: '3% 0', borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
            <div style={{ flex: 1 }}>Total fee to invest in this mintbox</div>
            <div style={{ paddingRight: '4%' }}>{Number(mintbox?.MintboxCost)?.toFixed(2) || 0.00}%</div>
          </div>
          <div className='smallTextLight' style={{ padding: '2% 0' }}>
            It cant get any cheaper than this! For comparison, the annual fee of a direct mutual fund is <b>1.00%</b>. That of regular mutual funds and PMS are <b>2.00%</b> and <b>2.50%</b> respectively.
          </div>
        </div>
      </div>
    )
  }

  const Details = () => {
    return (
      <div style={{ display: 'flex', justifyContent: 'space-between', margin: '4%' }}>
        <a href={`https://d3etfstcuyoos5.cloudfront.net/factsheets/${MINTBOXID}_factsheet.pdf`} target="_blank" style={{ textDecoration: 'none' }} download rel="noreferrer">
          <div style={{ padding: '16px 12px', background: cssVar.getPropertyValue('--lighter'), borderRadius: '8px', textAlign: 'center' }}>
            <img src={baseImgURL+"factsheet.svg"} width='24px' height='24px' style={{ marginBottom: '8px' }} />
            <div className='header2dark'>mintbox<br />Factsheet</div>
          </div>
        </a>
        <a href={`https://d3etfstcuyoos5.cloudfront.net/tearsheets/${MINTBOXID}_tearsheet.pdf`} target="_blank" style={{ textDecoration: 'none' }} download rel="noreferrer">
          <div style={{ padding: '16px 12px', background: cssVar.getPropertyValue('--lighter'), borderRadius: '8px', textAlign: 'center' }}>
            <img src={baseImgURL+"tearsheet.svg"} width='24px' height='24px' style={{ marginBottom: '8px' }} />
            <div className='header2dark'>Performance<br />Tearsheet</div>
          </div>
        </a>
        <a href={`https://mintbox.ai/blog/smart-low-cost-portfolios-hyper-customized-for-you%ef%bf%bc/`} target="_blank" style={{ textDecoration: 'none' }} download rel="noreferrer">
          <div style={{ padding: '16px 12px', background: cssVar.getPropertyValue('--lighter'), borderRadius: '8px', textAlign: 'center' }}>
            <img src={baseImgURL+"blog.svg"} width='24px' height='24px' style={{ marginBottom: '8px' }} />

            <div className='header2dark'>Investment<br />methodology</div>
          </div>
        </a>
      </div>
    )
  }

  const Footer = () => {
    return (
      <div className='footer' style={{ gap: "4%" }}>
        {
          mintbox.IsCustom === 1 ?
            <></>
            :
            <div>
              <div className='bodyTextDark' style={{ marginBottom: "1%" }}>Understand this mintbox</div>
              <div className='header3dark' style={{ color: cssVar.getPropertyValue("--secondaryGreen") }}
                onClick={() => setShowVideoModal(true)}>
                Watch explainer video <AiOutlineRight size={10} /></div>
            </div>
        }
        <div style={{ flex: 1 }}>
          <button
            className='solidButton'
            onClick={() => props.history.push({
              pathname: '/portfolio/confirm-invest',
              state: {
                fundData: { ...mintbox, MintboxId: MINTBOXID, Constituents: mintboxConstituents },
                type: 3
              }
            })}
          >
            Invest for free
          </button>
        </div>
      </div>
    )
  }
  const tabsObject = [
    { key: 1, title: 'Overview', view: Overview() },
    { key: 2, title: 'Constituents', view: Constituents() },
    { key: 3, title: 'Fee', view: Fee() },
    { key: 4, title: 'Details', view: Details() }
  ];

  const OtherMintboxes = () => {
    const riskMintboxes = {
      'header': `More ${RiskFormat(mintbox.RiskLevel)} mintboxes`,
      "description": "",
      "content": otherRiskMintboxes,
      // 'right': 'More',
      // 'rightAction': () => { props.history.push({ pathname: '/list', state: { type: 3, query: "RiskLevel", RISKLEVEL: mintbox.RiskLevel } }) }
    }
    const durationMintboxes = {
      'header': "More mintboxes ideal for " + mintbox.Duration,
      "description": "",
      "content": otherDurationMintboxes,
      // 'right': 'More',
      // 'rightAction': () => { props.history.push({ pathname: '/list', state: { type: 3, query: "DurationId", DURATIONID: mintbox.IdealDuration } }) }
    }
    return (
      <>
        <SliderComponent2 navigation={props.history} type="fundCard" fundType={3} component={riskMintboxes} />
        <SliderComponent2 navigation={props.history} type="fundCard" fundType={3} component={durationMintboxes} />
      </>
    )
  }

  return (
    <div className='mainContainer'>
      {
        loading ?
          <>
            <MainHeader
              header="mintbox"
              navigation={props.history}
            />
            <ActivityLoaderMintbox />
          </>
          :
          <>
            <MainHeader
              header="mintbox"
              isWatchlisted={isWatchlisted}
              onWatchlistIconClick={() => { updateWatchlist() }}
              right="bookmark-share"
              navigation={props.history}
            />
            <div className="dataContainer px-0">
              <Summary />
              {/* {mintbox.IsCustom === 0 ?
                <SipLumpChart fundCode={MINTBOXID} user={mintbox.ClientId} />
                :
                <SipLumpChart etfs={changeObjKey(mintboxConstituents, ["CurrentWeightage", "FundCode"], ["allocation", "Symbol"], "normal", "ty")} type={4} />
              } */}
              {
                mintbox.IsCustom === 1 ?
                  <Constituents />
                  :
                  <Tabs tabsObject={tabsObject} color={cssVar.getPropertyValue('--darkGreen')} />
              }
              {
                mintbox.IsCustom === 1 ?
                  <PerformanceChart etfs={changeObjKey(mintboxConstituents, ["CurrentWeightage", "FundCode"], ["allocation", "Symbol"], "normal", "ty")} type={4} />
                  :
                  <PerformanceChart fundCode={MINTBOXID} type={3} />
              }
              <OtherMintboxes />
            </div>
            <VideoModal />
            <Footer />
          </>
      }
    </div>
  )
}

export default MintboxDetail